'use strict'

const agentFixtures = require('./agent')

const metric = {
  id: 1,
  type: 'metric type',
  value: 'some random value for metric',
  agent: agentFixtures.byUuid('yyy-yyy-yyy'),
  createdAt: new Date(),
  updatedAt: new Date()
}

const metrics = [
  metric,
  extend(metric, { id: 2 }),
  extend(metric, { id: 3, type: 'Another type' }),
  extend(metric, { id: 4, agent: agentFixtures.byUuid('yyy-yyy-yyx') }),
  extend(metric, { id: 5, type: 'random type', agent: agentFixtures.byUuid('yyy-yyy-yyx') })
]

function extend (obj, value) {
  const clone = Object.assign({}, obj)
  return Object.assign(clone, value)
}

module.exports = {
  single: metric,
  all: metrics,
  byUuid: uuid => metrics.filter(m => m.agent.uuid === uuid).shift(),
  byTypeUuid: (type, uuid) => metrics.filter(m => m.type === type && m.agent.uuid === uuid).shift()
}
